.user-card {
  margin-bottom: 8px;

  &:hover {
    filter: drop-shadow(0 4px 9px rgba(0, 0, 0, 0.16));
    padding-bottom: 0;

    .user-card__info {
      padding-bottom: 16px;
    }

    .user__photo {
      transform: scale(1.02);
      transition: all 0.5s ease-out;
    }
  }
}

.user-card__tags {
  margin: 0 0 14px;
  text-align: center;
  max-width: 90.909%;

  @media (min-width: $tablet-width) {
    line-height: 23px;
    max-width: 100%;
  }
}

.user-card__photo {
  width: 100%;
  height: auto;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;

  @media (min-width: $tablet-width) {
    border-top-left-radius: 22px;
    border-top-right-radius: 22px;
  }
}

.user-card__name {
  font-size: 21px;
  line-height: 21px;
  margin-bottom: 15px;

  @media (min-width: $tablet-width) {
    font-size: 24px;
    line-height: 24px;
    margin-bottom: 26px;
    align-self: start;
    margin-left: 43px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 25px;
    align-self: center;
    margin-left: 0;
  }
}

.user-card__status {
  position: absolute;
  top: 8px;
  right: 22px;
}

.user-card__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: $white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 0 25px 20px;

  @media (min-width: $tablet-width) {
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 25px 15px;
  }
}

.user-card__wants {
  margin-top: -10px;

  @media (min-width: $tablet-width) {
    margin-top: -12px;
    padding-bottom: 15px;
  }
}

.user-card__level {
  position: absolute;
  top: 17px;
  left: 17px;
  width: 60px;
  height: 60px;
  color: $white;
  background-color: $black_06;

  @media (min-width: $tablet-width) {
    margin-top: 0;
    top: 20px;
    left: 22px;
  }
}

.user-card__wants-flag {
  width: 35px;
  height: 24px;
  border-radius: 2px;
}

.user-card__wants-item {
  position: relative;

  &:hover {
    .user-card__tooltip {
      display: flex;
      bottom: -69px;
    }
  }
}
