.page {
  &--inner {
    background-color: $grey_100;
  }
}

.page__body {
  position: relative;
  width: 100%;
  min-width: 320px;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: min-content 1fr min-content;
  grid-template-areas:
    "header"
    "main"
    "footer";
  min-height: 100%;
  margin: 0;
  font-family: "Bloggersans", "Trebuchet MS", "Calibri", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  color: $grey;
}
